*,
*::before,
*::after {
	box-sizing: border-box;
}

html {
	box-sizing: border-box;
}

html,
body {
	margin: 0;
	padding: 0;
}

body {
	background-color: var(--main-bg);
}

img,
video,
svg {
	display: block;
	max-width: 100%;
	height: auto;
}

textarea {
	resize: none;
	border-radius: 0;
}

textarea::placeholder {
	opacity: 1;
}

textarea:invalid {
	box-shadow: none;
}

select {
	border-radius: 0;
}

input::-ms-clear,
input::-ms-reveal {
	display: none;
}

input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
	appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
	margin: 0;

	appearance: none;
}

select,
textarea,
input:matches(
		[type="email"],
		[type="number"],
		[type="password"],
		[type="search"],
		[type="tel"],
		[type="text"],
		[type="url"]
	) {
	appearance: none;
}

button,
[type="button"],
[type="reset"],
[type="submit"] {
	appearance: none;
}

ul {
	padding-inline-start: 0px;
}
